import { _decorator, Component, EventTouch, instantiate, Node, Sprite, UITransform } from "cc";
import { basePageView } from "../common/basePageView";
import WebUtils from "../common/WebUtils";
const { ccclass, property } = _decorator;

@ccclass("discoverView")
export class discoverView extends basePageView {
  @property(Node)
  btn_shift_game: Node = null;

  @property(Node)
  btn_shift_ugc: Node = null;

  @property(Node)
  game_part: Node = null;

  @property(Node)
  ugc_part: Node = null;

  @property(Node)
  game_instance: Node = null;

  @property(Node)
  ugc_instance: Node = null;

  @property(Node)
  game_father_node: Node = null;

  @property(Node)
  guc_father_node: Node = null;

  shift_btns: Node[] = [];

  start() {
    this, (this.shift_btns = [this.btn_shift_game, this.btn_shift_ugc]);
  }

  update(deltaTime: number) {}

  shiftCurrentPlayType(btn: EventTouch, data: string) {
    this.shift_btns.forEach((node) => {
      node.getChildByName("selected").active = false;
      node.getChildByName("selected_lable").active = false;
      node.getChildByName("unselected_lable").active = true;
    });
    btn.currentTarget.getChildByName("selected").active = true;
    btn.currentTarget.getChildByName("selected_lable").active = true;
    if (data === "game") {
      this.game_part.active = true
      this.ugc_part.active = false
    } else {
      this.game_part.active = false
      this.ugc_part.active = true
    }
  }

  initGameViews(
    gameList: {
      desc: string;
      hot: number;
      id: number;
      icon: string;
      name: string;
      position: number;
      sort: number;
      tags: string[];
      type: number;
      url: string;
    }[]
  ) {
    gameList.forEach((game, index) => {
        this.game_father_node.getComponent(UITransform).width = gameList.length * 500 + 500
        const game_point = instantiate(this.game_instance)
        game_point.active = true
        WebUtils.getRemoteImg(game.icon, game_point.getChildByName('img'))
        this.game_father_node.addChild(game_point)
        game_point.setPosition( -1000 + index * 500, 0)
    });
  }

  initUgcViews(
    ugcList: {
      desc: string;
      hot: number;
      id: number;
      icon: string;
      name: string;
      position: number;
      sort: number;
      tags: string[];
      type: number;
      url: string;
    }[]
  ) {
    ugcList.forEach((ugc, index) => {
        this.guc_father_node.getComponent(UITransform).width = ugcList.length * 500 + 500
        const game_point = instantiate(this.ugc_instance)
        game_point.active = true
        WebUtils.getRemoteImg(ugc.icon, game_point.getChildByName('img'))
        this.guc_father_node.addChild(game_point)
        game_point.setPosition(index * 500, 0)
    });
  }

  currentPageClose() {}
}
